

.komanda__wrapper{
    position: relative;
    z-index: 20;
    backdrop-filter: blur(10px);
}
.komanda__container._right{
    margin: 50px 0 0 0;
}
.komanda__container{
    position: relative;
    z-index: 20;
    width: 70%;
    height: 100%;
    margin: 0 0 0 auto;
    overflow: hidden;
    
    .komanda__text{
        color: #fff;
        font-family: 'Bodoni Moda';
        text-transform: uppercase;
        font-weight: 700;
        h2{
            font-size: 5vw;
            display: flex;
            align-items: center;
            img{
                width: 200px;
                height: 100px;
                object-fit: cover;
                border-radius: 10px;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #dbd8d6;
            }
        }
        h3{
            font-size: 7vw;
            display: flex;
            align-items: center;
            img{
                width: 200px;
                height: 200px;
                object-fit: cover;
                border-radius: 10px;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #dbd8d6;
            }
        }
        h4{
            font-size: 3vw;
            display: flex;
            align-items: center;
            img{
                width: 200px;
                height: 100px;
                object-fit: cover;
                border-radius: 10px;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #dbd8d6;
            }
        }
        h5{
            font-size: 3vw;
            text-wrap: nowrap;
            display: flex;
            align-items: center;
            img{
                width: 200px;
                height: 100px;
                object-fit: cover;
                border-radius: 10px;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #dbd8d6;
            }
        }
    }
}
