$ruler: 16px;
$color-red: #AE1100;
$color-bg: #EBECF0;
$color-shadow: #BABECC;
$color-white: #FFF;


.footer{
    position: relative;
    z-index: 25;
    background-color: $color-bg;
    .footer__wrapper{
        display: flex;
        justify-content: space-between;
        .footer__container{
            background-color:$color-bg;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            font-family: 'Bai Jamjuree';
            
        .footer__logo{
          width: 700px;
          font-size: 10vw;
          color: blue;
          font-family: "BeatriceBold";
          text-transform: uppercase;

        }
        form {
            font-family: 'Bai Jamjuree';
            flex: 1;
            letter-spacing: 2px;
            font-size: $ruler;
            padding: $ruler;
            width: $ruler*20;
            margin-left: 10%;
          }
          .segment {
            padding: $ruler*2 0;
            text-align: center;
            color: $color-red;
            color:#61677C;
            text-shadow: 1px 1px 1px $color-white;
            font-size: 2vw;
          }
          
          button, input {
            border: 0;
            outline: 0;
            font-size: $ruler;
            border-radius: $ruler*20;
            padding: $ruler;
            background-color:$color-bg;
            text-shadow: 1px 1px 0 $color-white;
          }
          
          label {
            display: block;
            margin-bottom: $ruler*1.5;
            width: 100%;
          }
          
          input {
            margin-right: $ruler/2;
            box-shadow:  inset 2px 2px 5px $color-shadow, inset -5px -5px 10px $color-white;
            width: 100%;
            box-sizing: border-box;
            transition: all 0.2s ease-in-out;
            appearance: none;
            -webkit-appearance: none;
          
            &:focus {
              box-shadow:  inset 1px 1px 2px $color-shadow, inset -1px -1px 2px $color-white;
            }
          }
          
          button {
            color:#61677C;
            font-weight: bold;
            box-shadow: -5px -5px 20px $color-white,  5px 5px 20px $color-shadow;
            transition: all 0.2s ease-in-out;
            cursor: pointer;
            font-weight: 600;
            
            &:hover {
              box-shadow: -2px -2px 5px $color-white, 2px 2px 5px $color-shadow;
            }
            
            &:active {
              box-shadow: inset 1px 1px 2px $color-shadow, inset -1px -1px 2px $color-white;
            }
            
            &.red {
              display: block;
              width: 100%;
            }
          }
        }
        .footer__connect{
          margin: 0 0 5% 5%;
          font-weight: 100;
          ul{
            display: flex;
            width: 650px;
            .footer__connect__right{
              margin-left: 80px;
            }
            .footer__connect__item{
              font-size: 2vw;
              width: 270px;
              img{
                width: 40px;
                height: 40px;
                margin-right: 10px;
              }
              a{
                color: #000;
                position: relative;
                z-index: 20;
                width: 100%;
                &::after{
                  content: "";
                  position: absolute;
                  left: 0;
                  bottom: 50%;
                  height: 0%;
                  width: 270px;
                  background-color: #8894b0;
                  // background-color: #000;
                  transition:.3s ease-in-out;
                  z-index: 25;
                }
                &::before{
                  content: "";
                  position: absolute;
                  left: 0;
                  top: 50%;
                  height: 0%;
                  width: 270px;
                  background-color: #8894b0;
                  // background-color: #000;
                  transition:.3s ease-in-out;
                  z-index: 25;
                }
                span{
                  // font-size: 20px;
                  position: absolute;
                  left: 10px;
                  top: 0px;
                  height: 100%;
                  width: 270px;
                  color: #FFF;
                  opacity: 0;
                  transition: .3s ease-in-out;    
                }
              }:hover span{
                z-index: 30;
                opacity: 1;
              }
              :hover::after{
                height: 50%;
              }
              :hover::before{
                height: 50%;
              }
            }
            
            
          }
          
        }
        .footer__images{
            width: 50%;
        }
    }
}
