.main__bg{
    width: 100%;
    height: 100%;
}

.main__bg img{
    position: relative;
    width: 1200px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    z-index: 15;
    @media (max-width:1200px){
        width: 1000px;
    }

    @media (max-width:1000px){
        width: 850px;
    }
    @media (max-width:570px){
        width: 100%;
        object-fit: cover;
    }
}
.main__subbg__left{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
}
.main__subbg__right{
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
}