:root {
    --duration: 20s;
    --delay: 20s;
  }
    
.portfolio_bg{
    position: relative;
    z-index: 20;
    height: 100%;
    width: 100%; 
    .portfolio_bg_img{
        position: fixed;
        width: 120%;
    }
.portfolio_bg_wrapper{
    h2{
        margin: 0 auto;
        text-align: center;
        font-size: 14vw;
        color: #f5f0ec;
        font-weight: 700;
        text-rendering: geometricPrecision;
        letter-spacing: 8px;
    }
    
    .portfolio_bg_button{
        z-index: 12;
        position: relative;
        padding: 1vw 2vw;
        font-size: 2vw;
        background-color: #f5f0ec;
        color: #000;
        margin: 0px auto 50px;
        display: flex;
        justify-content: center;
        text-transform: uppercase;
        border-radius: 10px;
    }
}
.portfolio_bg_grid{
    width: 100%;
    width: 100vw;
    height: 100%;
    height: 1000px;
    height: 100vh;
    .portfolio_bg_grid_container{
        position: relative;
        height: 1200px;
        margin: 0 auto;
        padding: 0 1rem;
        width: 100vw;
        overflow: hidden;

        @mixin setImage($a, $x) {
        --name: float-up-#{$a};
        --duration: calc(20s * #{$a});
        left: #{$x}vw;
        z-index: -1 * $a;
}

img {
  --duration: 40s;
  --name: float-up-1;
  position: absolute;
  top: 0;
  left: 0;
  width: 33vw;
  transform: translateY(200vh);
  animation: var(--name) var(--duration) linear infinite;
  box-shadow: 1px 3px 15px rgba(0,0,0,0.5);
  border: 1px solid #f5f0ec;
  z-index: 1;

  @for $i from 1 through 10 {
    &:nth-child(#{$i}) {
      animation-delay: ($i - 1) * -3s;
    }
  }
  
  &:nth-child(1)  { @include setImage(1,  0) }
  &:nth-child(2)  { @include setImage(3, 10) }
  &:nth-child(3)  { @include setImage(2, 90) }
  &:nth-child(4)  { @include setImage(1, 36) }
  &:nth-child(5)  { @include setImage(3, 62) }
  &:nth-child(6)  { @include setImage(2, 15) }
  &:nth-child(7)  { @include setImage(2, 55) }
  &:nth-child(8)  { @include setImage(3,-20) }
  &:nth-child(9)  { @include setImage(1, 68) }
  &:nth-child(10) { @include setImage(2,  0) }
}

@keyframes float-up-3 {
  from {
    transform: translateY(200vh) translateZ(-50vh);
  }
  to {
    transform: translateY(-200vh) translateZ(-50vh);
  }
}

@keyframes float-up-2 {
  from {
    transform: translateY(150vh) translateZ(-25vh);
  }
  to {
    transform: translateY(-150vh) translateZ(-25vh);
  }
}

@keyframes float-up-1 {
  from {
    transform: translateY(100vh);
  }
  to {
    transform: translateY(-100vh);
  }
}

