.work__wrapper{
    position: relative;
    z-index: 20;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    h2{
        font-size: 22vw;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: #000;
    }
    .work__top{
        position: absolute;
        top: 0%;
        width: 100%;
        height: 50vh;
        background-color: #f5f0ec;
        overflow: hidden;
        .work__top_item{
            bottom: 5%;
        }
    }
    .work__center{
        width: 100%;
        height: 100vh;
        background-color: #000;
        color: #f5f0ec;
        display: flex;
        justify-content: center;
        align-items: center;
        h3{
            font-size: 10vw;
            margin: 0 auto;
            
        }
    }
    .work__bottom{
        position: absolute;
        bottom: 0%;
        width: 100%;
        height: 50vh;
        background-color: #f5f0ec;
        overflow: hidden;
        .work__bottom_item{
            top: 0%!important;
        }
    }
}