.gallery__wrapper{
  position: relative;
  z-index: 20;
  width: 100vw;
  height: 100vh;
  background-color: none;
    .gallery__item{
      width: 100vw;
      height: 100vh;
      color: white;
      background: linear-gradient(to right, blue, pink);
      display: flex;
      justify-content: space-between;
      .gallery__text{
        padding: 2vw 0 0 2vw;
        h4{
        line-height: 4vw;
        color: transparent;
        font-family: "Bodoni Moda";
        font-weight: 400;
        font-size: 3vw;
        letter-spacing: 10px;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke: 2px #dbd8d6;
        }
        h2{
        width: 50vw;
        line-height: 4.5vw;
        font-family: "Bai Jamjuree";
        font-weight: 600;
        font-size: 5vw;
        -webkit-font-smoothing: antialiased;
        margin-bottom: 30px;
      }
          h3{
              font-size: 1.5vw;
              font-weight: 600;
              font-family: 'Syncopate', sans-serif;
              color: #dbd8d6;
            }
      }
      .gallery__images{
        position: relative;

        .gallery__images__item__1{
          position: absolute;
          left: 65%;
          top: 60%;
          width: 60%;
          height: 80%;
          transform: translate(-50%,-50%);
          z-index: 30;
          animation: galleryitem_1 cubic-bezier(0.6,1,0.8,0) 3s infinite alternate;
          @keyframes galleryitem_1 {
            from{
              transform: translate(-50%,-50%);
              scale: 1;
            }
            to{
              transform: translate(-55%,-60%);
              scale: 0.93;
            }
          }
        }
        .gallery__images__item__2{
          position: absolute;
          left: 70%;
